import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";
import MyProjects from "../../containers/myprojects/MyProjects";
import MyArticles from "../../containers/myarticles/MyArticles";
import { Fade } from "react-reveal";
import "./ProjectsAndArticlesComponent.css";
import ProjectImg from "./ProjectImg";

class ProjArt extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="projart-main">
        <Header theme={theme} />
        <div className="basic-projart">
          <Fade bottom duration={2000} distance="40px">
            <div className="heading-div">
              <div className="heading-img-div">
                <ProjectImg theme={theme} />
              </div>
              <div className="heading-text-div">
                <h1 className="heading-text" style={{ color: theme.text }}>
                  My Projects and Articles
                </h1>
                <h3
                  className="heading-sub-text"
                  style={{ color: theme.secondaryText }}
                >
                  Scroll down below to view some projects and articles that I've
                  written.
                </h3>
              </div>
            </div>
          </Fade>
          <MyProjects theme={this.props.theme} />
          <MyArticles theme={this.props.theme} />
        </div>
        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default ProjArt;
