import React, { Component } from "react";
import "./MyArticlesCard.css";
import { Fade } from "react-reveal";

class MyArticlesCard extends Component {
  render() {
    const article = this.props.article;
    const theme = this.props.theme;
    return (
      <Fade bottom duration={2000} distance="20px">
        <div className="art-card">
          <div className="a-content">
            <a
              href={article.article_link}
              target="_blank"
              rel="noopener noreferrer"
            >
              <div className="a-content-overlay"></div>
              <div
                className="art-header"
                style={{ backgroundColor: article.color_code }}
              >
                <img
                  className="logo_img"
                  src={require(`../../assests/images/${article.logo_path}`)}
                  alt={article.alt_name}
                />
              </div>
              <div className="a-content-details fadeIn-top">
                <h3 className="a-content-title" style={{ color: theme.body }}>
                  Click Here to Read this Article
                </h3>
              </div>
            </a>
          </div>
          <div className="art-body">
            <figcaption className="a-figcapt">
              Image retrieved from{" "}
              <a
                href={article.imgurl}
                target="_blank"
                rel="noopener noreferrer"
              >
                {article.imgsource}
              </a>
            </figcaption>
            <h2 className="art-body-title" style={{ color: theme.text }}>
              {article.title}
            </h2>
            <h3
              className="art-body-subtitle"
              style={{ color: theme.secondaryText }}
            >
              {article.subtitle}
            </h3>
          </div>
        </div>
      </Fade>
    );
  }
}

export default MyArticlesCard;
