import React, { Component } from "react";
import "./MyProjects.css";
import MyProjectsCard from "../../components/myProjectsCard/MyProjectsCard";
import { projects } from "../../portfolio";
import { Fade } from "react-reveal";

class MyProjects extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="main" id="projs">
        <div className="projs-header-div">
          <Fade top duration={2000} distance="20px">
            <h1 className="projs-header" style={{ color: theme.text }}>
              Some Of My Projects
            </h1>
            <h4 className="projs-subheader">
              Here are some projects that I've worked on and have uploaded
              online. I do have some personal projects that I haven't uploaded
              online (at least, not yet - a lot of which includes stock market
              data analysis). If you'd like to see the projects that I haven't
              uploaded, please contact me, and in the meantime, feel free to
              check out and play around with my projects listed below!
            </h4>
          </Fade>
        </div>
        <div className="projs-body-div">
          {projects.projects.map((proj) => {
            return <MyProjectsCard project={proj} theme={theme} />;
          })}
        </div>
      </div>
    );
  }
}

export default MyProjects;
