import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import HackerImg from "./HackerImg";
import FullStopImg from "./FullStopImg";
import RoadsImg1 from "./RoadsImg1";
import { Fade } from "react-reveal";
import "./ContactComponent.css";
import { greeting, contactPageData } from "../../portfolio.js";
const ContactData = contactPageData.contactSection;
const blogSection = contactPageData.blogSection;

class Contact extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="contact-main">
        <Header theme={theme} />
        <div className="basic-contact">
          <Fade bottom duration={1000} distance="40px">
            <div className="contact-heading-div">
              <div className="contact-heading-img-div">
                <img
                  src={require(`../../assests/images/${ContactData["profile_image_path"]}`)}
                  alt=""
                />
              </div>
              <div className="contact-heading-text-div">
                <h1
                  className="contact-heading-text"
                  style={{ color: theme.text }}
                >
                  {ContactData["title"]}
                </h1>
                <p
                  className="contact-header-detail-text"
                  style={{ color: theme.secondaryText }}
                >
                  {ContactData["description"]}
                </p>
                <SocialMedia theme={theme} />
                <div className="resume-btn-div">
                  <Button
                    text="See My Resume"
                    newTab={true}
                    href={greeting.resumeLink}
                    theme={theme}
                  />
                </div>
              </div>
            </div>
          </Fade>
          <Fade bottom duration={1000} distance="40px">
            <div className="blog-heading-div">
              <div className="blog-heading-text-div">
                <h1 className="blog-heading-text" style={{ color: theme.text }}>
                  "{blogSection["title"]}"
                </h1>
                <h2
                  className="blog-subheading-text"
                  style={{ color: theme.text }}
                >
                  {blogSection["secondTitle"]}
                </h2>
                <p
                  className="blog-header-poem-text"
                  style={{ color: theme.secondaryText }}
                >
                  In this section,<br></br>
                  being the last,<br></br>
                  I'll dump to you,<br></br>
                  information in vast.<br></br>
                  <br></br>I must give credit,<br></br>
                  to where it's due.<br></br>
                  Without "failure",<br></br>I wouldn't know what to do.
                  <br></br>
                  <br></br>
                  First generation,<br></br>
                  first born,<br></br>
                  unpredictable outcomes,<br></br>
                  unpredictable storms.<br></br>
                  <br></br>
                  Many roads,<br></br>
                  many opportunities.<br></br>
                  Couldn't ask for everything,<br></br>
                  yet wouldn't accept nothing.<br></br>
                  <br></br>
                  Wanted money,<br></br>
                  wanted fame.<br></br>
                  Had to play my cards right,<br></br>
                  to still stay in this game.<br></br>
                  <br></br>
                  Options, options,<br></br>
                  had many options,<br></br>
                  some were good,<br></br>
                  many were bad.<br></br>
                  <br></br>
                  Wanted to feed this brain,<br></br>
                  the knowledge of the universe.<br></br>
                  Decided to stay in school,<br></br>
                  decided to pick up a book.<br></br>
                  <br></br>
                  Musical instruments and poetry,<br></br>
                  acting and directing,<br></br>
                  some roads I could have took.<br></br>
                  Analysis of the success rate?<br></br>
                  Had to pick up a different book.<br></br>
                  <br></br>
                  Needed to broaden my vocabulary,<br></br>
                  needed to change my lifestyle.<br></br>
                  <br></br>
                  Chose to run different races,<br></br>
                  all which were of different paces.<br></br>
                  Some long, others short,<br></br>
                  Some fast, others slow.<br></br>
                  <br></br>
                  Didn't know which one to stick to,<br></br>
                  kept getting knocked down.<br></br>
                  Had to find a steady race quickly,<br></br>
                  had to work my way up.<br></br>
                  <br></br>
                  The more books I've read,<br></br>
                  the less races I've chose to run.<br></br>
                  Collecting, wrangling, modeling,<br></br>
                  evaluating and finally analyzing.<br></br>
                  Thinking I've found my craft,<br></br>
                  thinking I've found the one.<br></br>
                  <br></br>
                  Growing up,<br></br>
                  billiards and bodybuilding,<br></br>
                  hiking and exploring,<br></br>
                  kept me inline.<br></br>
                  Overtime,<br></br>
                  fishing and kayaking,<br></br>
                  betting and options trading,<br></br>
                  became newer hobbies of mine.<br></br>
                  <br></br>
                  Time complexity worsened,<br></br>
                  as things stacked up.<br></br>
                  Realizing life is short,<br></br>
                  had to pop things down.<br></br>
                  Had to balance out my life,<br></br>
                  had to balance out my time.<br></br>
                  <br></br>
                  Without a doubt,<br></br>
                  chose the better path.<br></br>
                  Knowing it'll add up soon,<br></br>
                  just need to cultivate my craft.<br></br>
                  <br></br>
                  Success engraved within failure,<br></br>
                  is what I'm chasing,<br></br>
                  while steadily pacing,<br></br>
                  down this road I'm racing.<br></br>
                  <br></br>
                  This is just a "Story in the Making".
                  {/* {blogSection["poem"]} */}
                </p>
                {/* <p
                  className="blog-header-detail-text subTitle"
                  style={{ color: theme.secondaryText }}
                >
                  {blogSection["subtitle"]}
                </p> */}
                {/* <div className="blogsite-btn-div">
                  <Button
                    text="Click here"
                    newTab={false}
                    href={blogSection.link}
                    theme={theme}
                  />
                </div> */}
              </div>
              {/* <div className="blogs-heading-img-div-pack"> */}
              <div className="blog-heading-img-div">
                {/* <img
                        src={require(`../../assests/images/${blogSection["avatar_image_path"]}`)}
                        alt=""
                      /> */}
                <RoadsImg1 theme={theme} />
                <div className="fullstop-imgs">
                  <FullStopImg theme={theme} />
                  <FullStopImg theme={theme} />
                  <FullStopImg theme={theme} />
                </div>
                <div className="hacker-img">
                  <HackerImg theme={theme} />
                </div>
              </div>
            </div>
          </Fade>
        </div>
        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default Contact;
