import React, { Component } from "react";
import "./MyArticles.css";
import { Fade } from "react-reveal";
import { articles } from "../../portfolio";
import MyArticlesCard from "../../components/myArticlesCard/MyArticlesCard";

class MyArticles extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="main" id="arts">
        <div className="arts-header-div">
          <Fade top duration={2000} distance="20px">
            <h1 className="arts-header" style={{ color: theme.text }}>
              My Articles
            </h1>
            <h3 className="arts-subheader">
              Here are some articles which I've written.<br></br>Click on the
              image to read the corresponding article.
            </h3>
          </Fade>
        </div>
        <div className="arts-body-div">
          {articles.anArticle.map((art) => {
            return <MyArticlesCard article={art} theme={theme} />;
          })}
        </div>
      </div>
    );
  }
}

export default MyArticles;
