import React, { Component } from "react";
import "./MyProjectsCard.css";
import { Fade } from "react-reveal";

class MyProjectsCard extends Component {
  render() {
    const project = this.props.project;
    const theme = this.props.theme;
    return (
      <div className="project-card">
        <Fade left duration={2000} distance="40px">
          <div className="p-card-img">
            <img
              style={{
                maxWidth: "100%",
                maxHeight: "100%",
              }}
              className="proj_img"
              src={require(`../../assests/images/${project.logo_path}`)}
              alt={project.alt_name}
            />
            <figcaption className="p-figcapt">
              Image retrieved from{" "}
              <a
                href={project.imgurl}
                target="_blank"
                rel="noopener noreferrer"
              >
                {project.imgsource}
              </a>
            </figcaption>
          </div>
        </Fade>
        <Fade right duration={2000} distance="40px">
          <div className="p-card-body">
            <div
              className="p-body-header"
              style={{ backgroundColor: "#FF3A52" }}
            >
              <div className="p-body-header-title">
                <h2 className="p-card-title" style={{ color: theme.text }}>
                  {project.title}
                </h2>
                <h3 className="p-card-subtitle" style={{ color: theme.text }}>
                  {project.subtitle}
                </h3>
              </div>
            </div>
            <div classname="p-body-content">
              {project.descriptions.map((sentence) => {
                return (
                  <p className="p-content-list" style={{ color: theme.text }}>
                    {sentence}
                  </p>
                );
              })}
              <a
                href={project.website_link}
                target="_blank"
                rel="noopener noreferrer"
              >
                <div
                  className="p-visit-btn"
                  style={{ backgroundColor: theme.headerColor }}
                >
                  <p className="p-btn" style={{ color: theme.text }}>
                    Click to View Project
                  </p>
                </div>
              </a>
            </div>
          </div>
        </Fade>
      </div>
    );
  }
}

export default MyProjectsCard;
